import React from "react";
import { Route, Switch } from "react-router-dom";
import AppHeader from "./app-template/AppHeader";
import AppFooter from "./app-template/AppFooter";
import Landing from "./folio-landing/Landing";
import IEPView from "./folio-case-study-iep/IEPView";
import SearchView from "./folio-case-study-search/SearchView";
import BookView from "./folio-case-study-book/BookView";
import ResumeView from "./resume/ResumeView";
import NotFoundPage from "./NotFoundPage";

function App() {
  return (
    <>
      <AppHeader />

      <Switch>
        <Route path="/" exact component={Landing} />
        {/* <Route path="/overview" component={Landing} /> */}
        {/* <Redirect from="/" to="/overview" /> */}
        <Route path="/ingenio-eligibility-application" component={IEPView} />
        <Route path="/iglu-search-holiday" component={SearchView} />
        <Route path="/iglu-booking-flow" component={BookView} />
        <Route path="/resume" component={ResumeView} />
        <Route component={NotFoundPage} />
      </Switch>

      <AppFooter />
    </>
  );
}

export default App;
