import React from "react";
import ImageSticky from "../img/igl-srp-stickies3.jpg";

function SearchProgressive() {
  return (
    <section className="search-progressive">
      <article className="content">
        <h2 className="title">Design approach: progressive enhancement</h2>
        <p>
          As a new front-end team inheriting a very dated e-commerce platform,
          we had to be very clear on priorities as the core experiences were
          well below any standard.
        </p>
        <p>
          However, online bookings and sales enquiries to the call center were
          being made via the 'old' site. All new designs released could not work
          against what was already succesful.
        </p>
        <p>
          I progressively enhanced the existing site through regular releases of
          new designs, all A/B tested. I achieved a balance of evolving the site
          while ensuring new components released were not radically different to
          the overall site as to create a very disjointed experience.
        </p>
      </article>
      <figure className="imagery">
        <img src={ImageSticky} alt="affinity mapping" />
        <figcaption>
          The on-site search experience was well below holiday-industry
          standards. Instead of 're-designing' the whole experience and taking a
          lot of time, I released modular improvements on a regular basis.
        </figcaption>
      </figure>
    </section>
  );
}

export default SearchProgressive;
